.list{
    color: black;
    width: 300px;
    background-color: white;
    height: 50px;
    margin: 20px auto;
    border-radius: 5px;
    
    
}

.list p{
    padding: 13px;
    position: relative;
    margin-bottom: auto;
    
    
}
.list p span{
    position: absolute; 
    right: 10px;
    cursor: pointer;
}

.faicons{
    margin: 0.5px;
    font-size: 15px
}

.list p input{
    background-color: white;
    border: 0;
    color: black;
    font-size: 18px;
}

.list p input:focus{
    outline: none;
}